import React from 'react'
import { Doughnut, Pie } from 'react-chartjs-2'
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';

ChartJS.register(ArcElement, Tooltip, Legend)

function PieDoughnutChart() {

    const options = {
        responsive: true
    }

    const data = {
        labels: ['one', 'two', 'three', 'four', 'five', 'six'],
        datasets: [
            {
                label: "kuldip",
                data: [23, 43, 3, 5, 6, 23],
                backgroundColor: [
                    'rgba(255, 99, 132, 0.3)',
                    'rgba(54, 162, 235, 0.3)',
                    'rgba(255, 206, 86, 0.3)',
                    'rgba(75, 192, 192, 0.3)',
                    'rgba(153, 102, 255,0.3)',
                    'rgba(255, 159, 64, 0.3)',
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)',
                ]
            }
        ]
    }

    return (
        <>
            {/* always use height of component for good possitionning of Pie Chart */}
            <h1>Pie Chart</h1>
            <div style={{ height: '500px' }}>
                <Pie data={data} options={options} />
            </div>
            <div style={{ height: '500px' }}>
                <Doughnut data={data} options={options} />
            </div>
        </>
    )
}

export default PieDoughnutChart