import React from 'react' 
import {
    Chart as ChartJS,
    LinearScale,
    CategoryScale,
    BarElement,
    PointElement,
    LineElement,
    Legend,
    Tooltip,
    LineController,
    BarController,
  } from 'chart.js';
import { Chart } from 'react-chartjs-2';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    BarElement, 
    Tooltip, 
    Legend,
    LineController,
    BarController,
)


function MixChart() {

    const options = {
        plugins: {
            legend: {
                position: 'top'
            },
            title: {
                display: true,
                text: 'Chart.js Bar Chart - MAin Title',
            },
        },
        responsive: true
    };

    const data = {
        labels: ['one', 'two', 'three', 'four', 'five', 'six'],
        datasets: [
            {
                type: 'line',
                fill:false,
                label: "sarvaiya",
                data: [55, 4, -13, 65, 36, 13], 
                borderWidth: 10,
                borderColor: 'black'
            },
            {
                type: 'bar',
                label: "kuldip",
                data: [-23, 43, 43, 5, -6, 23],
                backgroundColor: 'red',
                borderWidth: '5px',
                borderColor: 'blue'
            },
            {
                type: 'bar',
                label: "KishorBhai",
                data: [13, 50, -33, 35, 16, -23],
                backgroundColor: 'green',
                borderWidth: '5px',
                borderColor: 'red'
            }
        ]
    }

    return (
        <div className='bar-chart' >
        <h1>Mix Chart</h1>
            <Chart data={data} options={options} />
        </div>
    )
}

export default MixChart