import React from 'react'
import { Bar } from 'react-chartjs-2'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
)

function BarChart() {

  const options = {
    plugins: {
      legend: {
        position: 'top' //possition of scale[praman map] indicator 
      },
      title: {
        display: true,
        text: 'Chart.js Bar Chart - MAin Title',
      },
    },
    responsive: true
    // scales: {
    //   x: {
    //     stacked: true,
    //   },
    //   y: {
    //     stacked: true,
    //   },
    // }
    // all bar in one bar - one bAR divided 
  };

  const data = {
    labels: ['one', 'two', 'three', 'four', 'five', 'six'],
    datasets: [
      {
        label: "kuldip",
        data: [-23, 43, 43, 5, -6, 23],
        backgroundColor: 'red',
        borderWidth: '5px',
        borderColor: 'blue'
      },
      {
        label: "KishorBhai",
        data: [13, 50, -33, 35, 16, -23],
        backgroundColor: 'green',
        borderWidth: '5px',
        borderColor: 'red'
      },
      {
        label: "sarvaiya",
        data: [55, 4, -13, 65, 36, 13],
        backgroundColor: 'blue',
        borderWidth: '5px',
        borderColor: 'green'
      }
    ]
  }

  return (
    <div className='bar-chart' >
    <h1>Bar Chart</h1>
      <Bar data={data} options={options} />
    </div>
  )
}

export default BarChart