import React from 'react';
import {
  Chart as ChartJS,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bubble, Scatter } from 'react-chartjs-2';

ChartJS.register(LinearScale, PointElement, LineElement, Tooltip, Legend);


function ScatterBubbleChart() {
  const options = {
    responsive: true
  };

  const data = {
    datasets: [
      {
        label: 'kuldip',
        data: [
          { x: 10, y: 23 },
          { x: 20, y: 56 },
          { x: 9, y: 20 },
          { x: 54, y: 35 },
          { x: 34, y: 45 }
        ],
        backgroundColor: 'red',
      },
      {
        label: 'sarvaiya',
        data: [
          { x: 32, y: 13 },
          { x: 50, y: 10 },
          { x: 34, y: 60 },
          { x: 4, y: 35 },
          { x: 48, y: 41 }
        ],
        backgroundColor: 'blue',
      }
    ],
  };

  const data2 = {
    datasets: [
      {
        label: 'kuldip',
        data: [
          { x: 10, y: 23, r:10  },
          { x: 20, y: 56, r:20  },
          { x: 9, y: 20 , r:30 },
          { x: 54, y: 35, r:40  },
          { x: 34, y: 45, r:50  }
        ],
        backgroundColor: 'red',
      },
      {
        label: 'sarvaiya',
        data: [
          { x: 32, y: 13, r:5 },
          { x: 50, y: 10, r:15 },
          { x: 34, y: 60, r:25 },
          { x: 4, y: 35 , r:35},
          { x: 48, y: 41, r:45 }
        ],
        backgroundColor: 'blue',
      }
    ],
  };

  return (
    <>
    <h1>Bubble Chart</h1>
      <Scatter options={options} data={data} />
      <br/>
      <br/>
      <h1>The only Diffrence in Bubble Chart is that it can accept third value in data that indicates the size of BUBBLE</h1>
      <Bubble options={options} data={data2} />
    </>
  )
}

export default ScatterBubbleChart