import React from 'react'
import { Line } from 'react-chartjs-2'
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend,
} from 'chart.js';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend,
)

function LineChats() {

    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: 'Chart.js Line Chart',
            },
        }
    };

    const data = {
        labels: ['one', 'two', 'three', 'four', 'five', 'six'],
        datasets: [
            {
                fill: false,
                label: "kuldip",
                data: [23, 43, 43, 5, 6, 23],
                backgroundColor: 'red',
                borderColor: 'red'
            },
            {
                fill: true,
                label: "kishorbhai",
                data: [13, 50, -33, 35, 16, -23],
                backgroundColor: 'rgba(0,255,0,0.4)',
                borderColor: 'green'
            },
            {
                fill: false,
                label: "sarvaiya",
                data: [55, 4, -13, 65, 36, 13],
                backgroundColor: 'blue',
                borderColor: 'blue'
            }
        ]
    }

    return (
        <>
            <h1>Line Chart</h1>
            <Line data={data} options={options} />
        </>
    )
}

export default LineChats